<template>
  <div id="app">
    <choose-user v-if="$root.me==null" :userlist='userlist'></choose-user>

    <userlist :islogin='islogin' :users="users" :chooseUser="chooseUser" :unreadlist="unreadlist"></userlist>
    <chat-user v-if="ischat" :touser="touser" :closeChat="closeChat" :newMsg='newMsg'></chat-user>
  </div>
</template>

<script>
import chooseUser from './components/chooseUser'
import axios from 'axios'
import userlist from './components/userlist'
import socket from './socket';
import chatUser from './components/chatUser'
// import io from 'socket.io-client'

export default {
  name: 'app',
  components: {
    chooseUser,
    userlist,
    chatUser,
  },
  data(){
    return {
      userlist:[],
      islogin:false,
      users:[],
      ischat:false,
      touser:null,
      unreadlist:[],
      newMsg:null
    }
  },
  // beforeMount(){
  //   axios.get('http://localhost:3000/api/userlist').then(function(res){
  //     console.log(res)
  //   })
  async beforeMount(){
    let res = await axios.get('http://localhost:3000/api/userlist')
    this.userlist = res.data
    console.log(this.$root)
  },
  mounted(){
    // let socket = io.connect('http://localhost:3000')
    // //console.log(socket)
    // socket.on('connect',function(){
    //   console.log('与服务器建立起socket连接')
    // })
    //监听登录成功，设置为true
    socket.on('login',(data)=>{
      if(data.state == 'ok'){
        this.islogin = true;
        socket.emit('users')
      }
    })

    //监听登出事件
    socket.on('logout',(data)=>{
      console.log(data)
      this.islogin = false;
      //断开连接
      socket.disconnect()
    })

    //监听断开连接事件
    socket.on('disconnect',(data)=>{
      console.log('断开连接')
    })

  socket.on('users',(data)=>{
    console.log(data)
    this.users = data
  })

  socket.on('unreadMsg',(data)=>{
    // console.log('unreadMsg')
     //console.log(data)
     data.forEach((item,index)=>{
        //设置未读的红点
        //将聊天的内容分别添加到本地的存储
        //将from/to改成由头像的对象
        item.from = this.usersObj[item.from]
        item.to = this.usersObj[item.to]
        this.unreadlist.push(item.from);
        
        let strKey = 'chat-user-'+this.$root.me.username+'-'+item.from.username;
        //先解析本地存储的数据，在添加
        //console.log(localStorage[strKey])
        localStorage[strKey] = localStorage[strKey]?localStorage[strKey]:'[]';
        let newArr = JSON.parse(localStorage[strKey]);
        newArr.push(item)
        localStorage[strKey] = JSON.stringify(newArr);
        console.log(this.usersObj)

      })
     // console.log(this)
  })
  socket.on('accept',(msg)=>{
      console.log('--------接收的数据')
      console.log(msg)
      //console.log(this.ischat)
      //console.log(this.touser)
      if((this.ischat==true && msg.from.username==this.touser.username)||(msg.to.username==this.touser.username&&msg.to.isgroup=='true') ){
        this.newMsg = msg;
        
      }else{
        let strKey = 'chat-user-'+this.$root.me.username+'-'+msg.from.username;
        //先解析本地存储的数据，在添加
        //console.log(localStorage[strKey])
        localStorage[strKey] = localStorage[strKey]?localStorage[strKey]:'[]';
        let newArr = JSON.parse(localStorage[strKey]);
        newArr.push(msg)
        localStorage[strKey] = JSON.stringify(newArr);
        this.unreadlist.push(msg.from.username)
      }
  })

  },
  methods:{
    chooseUser:function(user){
      this.touser = user;
      this.ischat = true;
      let index = this.unreadlist.indexOf(user.username)
      this.unreadlist.splice(index,1);
    },
    closeChat:function(){
      this.ischat = false;
    }
  },
  computed:{
    usersObj:function(){
      let obj = {}
      this.users.forEach((item,index)=>{
        obj[item.username] = item;
      })
     // console.log(obj)
      return obj;
    }
  },
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
</style>
